// -----------------------------------------------------------------------------
// This file contains all styles related to the modal component.
// -----------------------------------------------------------------------------

/**
* Modal component
**/
.modal {
	z-index: 2;
	position: absolute;
	padding: 1rem 2rem;
	border-radius: 0.5rem;
	background: #fff;
	box-shadow: 0 0 0.625rem #000;
	.close-modal {
		position: absolute;
		top: -0.8rem;
		right: -0.8rem;
		width: 1.9rem;
		height: 1.9rem;
		border: none;
		background-color: transparent;
		background-image: url("data:image/png;base64,");
		background-size: contain;
		text-indent: -624.9375rem;
		cursor: pointer;
	}
	&-background {
		z-index: 1;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.75);
	}
}